@use 'base/variables' as v;
@use 'base/mixins' as m;

.polls-carrousel{

  h2{
    margin-bottom: 2rem;
  }
  .header-container{
    display: flex;
    justify-content: space-between;
    align-items: center;

    h2{
      text-align: start;
      margin-bottom: 1rem;
    }

    a{
      font-size: 3rem;
      color: v.$principalColor;
      transition: color .2s ease;

      &:hover{
        color: lighten($color: v.$principalColor, $amount: 10);
      }
    }
  }

  .glide{
    border-radius: 2rem;
    overflow: hidden;
    
    .about-poll-container{
      position: relative;
      height: 100%;

      img{
        width: 100%;
        max-height: 30rem;
        object-fit: cover;
      }

      .about-poll{
        background-color: rgba($color: v.$black, $alpha: 0.8);
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        padding: .7rem 1rem;
        
        h3{
          text-align: start;
          font-size: 3rem;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
        }

        .category-link{
          color: v.$gray;
          transition: color .3s ease;

          &:hover{
            color: v.$principalColor;
            cursor: pointer;
          }
        }
      }
    }
  }
}